.sidebar {
    flex: 1;
    border-right: 0.5px solid lightgray;
    background-color: white;
    min-height: 100vh;
    max-height: 100vh;
    overflow-y: auto;

    .top {
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;

        .logo {
            font-size: 20px;
            font-weight: bold;
            color: #6439ff;
        }
    }

    hr {
        height: 0;
        border: 0.5px solid lightgray;
    }

    .center {
        padding: 0 10px;

        .title {
            margin-top: 15px;
            margin-bottom: 5px;
            font-size: 12px;
            font-weight: bold;
            color: #999;
        }

        ul {
            list-style: none;
            padding: 0;

            li {
                display: flex;
                align-items: center;
                cursor: pointer;
                margin-bottom: 5px;
                padding: 5px;
                border-radius: 5px;

                &:hover {
                    background-color: #ece8ff;
                }

                .icon {
                    font-size: 18px;
                    color: #7451f8;
                }

                span {
                    font-size: 18px;
                    font-weight: 600;
                    color: #888;
                    margin-left: 10px;
                }
            }
        }
    }

    .bottom {
        display: flex;
        align-items: center;
        margin: 10px;

        .colorOption {
            width: 20px;
            height: 20px;
            border-radius: 5px;
            border: 1px solid #7451f8;
            margin: 5px;

            &:nth-child(1) {
                background-color: whitesmoke;
            }

            &:nth-child(2) {
                background-color: darkslategray;
            }
        }
    }
}